@mixin code-theme-light {

  --code-block-background-color: #19212C;
  --code-line-numbers-background-color: #{darken(#19212C, 2)};

  // --code-background: #{$code-background-dark};
  // --code-accent-color: #{darken($code-background-dark, 4)};
  // --code-accent-color-lite: #{darken($code-background-dark, 2)};
  // --code-font-color: #{$slate};

  // --code-copy-background: #{$body-background-dark};
  // --code-copy-font-color: #{lighten($body-font-color, 24)};
  // --code-copy-border-color: #{lighten($body-font-color, 24)};
  // --code-copy-success-color: rgba(0, 200, 83, .5);


  /* Theme: Dracula */

  /* Background */
  .bg {
    color: #f8f8f2;
    background-color: #272822;
  }
  /* PreWrapper */
  .chroma {
    color: #f8f8f2;
    background-color: #272822;
  }
  /* Other */
  .chroma .x {
  }
  /* Error */
  .chroma .err {
    color: #960050;
    background-color: #1e0010;
  }
  /* CodeLine */
  .chroma .cl {
  }
  /* LineTableTD */
  .chroma .lntd {
    vertical-align: top;
    padding: 0;
    margin: 0;
    border: 0;
  }
  /* LineTable */
  .chroma .lntable {
    border-spacing: 0;
    padding: 0;
    margin: 0;
    border: 0;
  }
  /* LineHighlight */
  .chroma .hl {
    background-color: #ffffcc;
  }
  /* LineNumbersTable */
  .chroma .lnt {
    white-space: pre;
    user-select: none;
    margin-right: 0.4em;
    padding: 0 0.4em 0 0.4em;
    color: #b2b2b2;
  }
  /* LineNumbers */
  .chroma .ln {
    white-space: pre;
    user-select: none;
    margin-right: 0.4em;
    padding: 0 0.4em 0 0.4em;
    color: #7f7f7f;
  }
  /* Line */
  .chroma .line {
    display: flex;
  }
  /* Keyword */
  .chroma .k {
    color: #66d9ef;
  }
  /* KeywordConstant */
  .chroma .kc {
    color: #66d9ef;
  }
  /* KeywordDeclaration */
  .chroma .kd {
    color: #66d9ef;
  }
  /* KeywordNamespace */
  .chroma .kn {
    color: #f92672;
  }
  /* KeywordPseudo */
  .chroma .kp {
    color: #66d9ef;
  }
  /* KeywordReserved */
  .chroma .kr {
    color: #66d9ef;
  }
  /* KeywordType */
  .chroma .kt {
    color: #66d9ef;
  }
  /* Name */
  .chroma .n {
  }
  /* NameAttribute */
  .chroma .na {
    color: #a6e22e;
  }
  /* NameBuiltin */
  .chroma .nb {
  }
  /* NameBuiltinPseudo */
  .chroma .bp {
  }
  /* NameClass */
  .chroma .nc {
    color: #a6e22e;
  }
  /* NameConstant */
  .chroma .no {
    color: #66d9ef;
  }
  /* NameDecorator */
  .chroma .nd {
    color: #a6e22e;
  }
  /* NameEntity */
  .chroma .ni {
  }
  /* NameException */
  .chroma .ne {
    color: #a6e22e;
  }
  /* NameFunction */
  .chroma .nf {
    color: #a6e22e;
  }
  /* NameFunctionMagic */
  .chroma .fm {
  }
  /* NameLabel */
  .chroma .nl {
  }
  /* NameNamespace */
  .chroma .nn {
  }
  /* NameOther */
  .chroma .nx {
    color: #a6e22e;
  }
  /* NameProperty */
  .chroma .py {
  }
  /* NameTag */
  .chroma .nt {
    color: #f92672;
  }
  /* NameVariable */
  .chroma .nv {
  }
  /* NameVariableClass */
  .chroma .vc {
  }
  /* NameVariableGlobal */
  .chroma .vg {
  }
  /* NameVariableInstance */
  .chroma .vi {
  }
  /* NameVariableMagic */
  .chroma .vm {
  }
  /* Literal */
  .chroma .l {
    color: #ae81ff;
  }
  /* LiteralDate */
  .chroma .ld {
    color: #e6db74;
  }
  /* LiteralString */
  .chroma .s {
    color: #e6db74;
  }
  /* LiteralStringAffix */
  .chroma .sa {
    color: #e6db74;
  }
  /* LiteralStringBacktick */
  .chroma .sb {
    color: #e6db74;
  }
  /* LiteralStringChar */
  .chroma .sc {
    color: #e6db74;
  }
  /* LiteralStringDelimiter */
  .chroma .dl {
    color: #e6db74;
  }
  /* LiteralStringDoc */
  .chroma .sd {
    color: #e6db74;
  }
  /* LiteralStringDouble */
  .chroma .s2 {
    color: #e6db74;
  }
  /* LiteralStringEscape */
  .chroma .se {
    color: #ae81ff;
  }
  /* LiteralStringHeredoc */
  .chroma .sh {
    color: #e6db74;
  }
  /* LiteralStringInterpol */
  .chroma .si {
    color: #e6db74;
  }
  /* LiteralStringOther */
  .chroma .sx {
    color: #e6db74;
  }
  /* LiteralStringRegex */
  .chroma .sr {
    color: #e6db74;
  }
  /* LiteralStringSingle */
  .chroma .s1 {
    color: #e6db74;
  }
  /* LiteralStringSymbol */
  .chroma .ss {
    color: #e6db74;
  }
  /* LiteralNumber */
  .chroma .m {
    color: #ae81ff;
  }
  /* LiteralNumberBin */
  .chroma .mb {
    color: #ae81ff;
  }
  /* LiteralNumberFloat */
  .chroma .mf {
    color: #ae81ff;
  }
  /* LiteralNumberHex */
  .chroma .mh {
    color: #ae81ff;
  }
  /* LiteralNumberInteger */
  .chroma .mi {
    color: #ae81ff;
  }
  /* LiteralNumberIntegerLong */
  .chroma .il {
    color: #ae81ff;
  }
  /* LiteralNumberOct */
  .chroma .mo {
    color: #ae81ff;
  }
  /* Operator */
  .chroma .o {
    color: #f92672;
  }
  /* OperatorWord */
  .chroma .ow {
    color: #f92672;
  }
  /* Punctuation */
  .chroma .p {
  }
  /* Comment */
  .chroma .c {
    color: #75715e;
  }
  /* CommentHashbang */
  .chroma .ch {
    color: #75715e;
  }
  /* CommentMultiline */
  .chroma .cm {
    color: #75715e;
  }
  /* CommentSingle */
  .chroma .c1 {
    color: #75715e;
  }
  /* CommentSpecial */
  .chroma .cs {
    color: #75715e;
  }
  /* CommentPreproc */
  .chroma .cp {
    color: #75715e;
  }
  /* CommentPreprocFile */
  .chroma .cpf {
    color: #75715e;
  }
  /* Generic */
  .chroma .g {
  }
  /* GenericDeleted */
  .chroma .gd {
    color: #f92672;
  }
  /* GenericEmph */
  .chroma .ge {
    font-style: italic;
  }
  /* GenericError */
  .chroma .gr {
  }
  /* GenericHeading */
  .chroma .gh {
  }
  /* GenericInserted */
  .chroma .gi {
    color: #a6e22e;
  }
  /* GenericOutput */
  .chroma .go {
    color: #6cb6d4;
  }
  /* GenericPrompt */
  .chroma .gp {
  }
  /* GenericStrong */
  .chroma .gs {
    font-weight: bold;
  }
  /* GenericSubheading */
  .chroma .gu {
    color: #75715e;
  }
  /* GenericTraceback */
  .chroma .gt {
  }
  /* GenericUnderline */
  .chroma .gl {
  }
  /* TextWhitespace */
  .chroma .w {
  }
}
